import React from 'react'

import { Header, Price, AboutUs, Review, ContactUs } from './container';
import {NavBar, Footer} from './components';
import { Outlet } from 'react-router-dom';
import './App.css';


const App = () => (
    <div>
        <Outlet />
        <NavBar /> 
        <Header />
        <Price />
        <AboutUs />    
        <Review />    
        <ContactUs />     
        <Footer />  
    </div>
);

export default App